* {
	font-family: 'Roboto', sans-serif; }

header {
	position: relative;
	height: 50px; }

nav {
	position: fixed;
	width: 100%;
	height: 50px;
	background-color: #212121;
	z-index: 2; }

.container {
	width: 1140px; }

.menu {
	display: flex;
	justify-content: space-between;
	padding: 13px 70px;
	margin: 0;
	list-style-type: none;
	&_link {
		color: #ffffff;
		font-size: 15px;
		font-weight: 300; }
	&_item {
		border-right: 2px dotted black;
		padding-right: 28px; } }

.subheader {
	position: absolute;
	top: 50px;
	width: 100%;
	height: 84px;
	background-color: rgba(0, 0, 0, 0.6);
	&_logo {
		display: block;
		max-width: 100%;
		margin-top: 16px; }
	&_official {
		opacity: 0.5;
		color: #ffffff;
		font-size: 13px;
		font-weight: 400;
		margin-top: 6px; }
	&_call {
		color: #ffffff;
		font-size: 12px;
		font-weight: 300;
		margin-top: 19px;
		text-align: right;
		&_btn {
			width: 172px;
			height: 46px;
			box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
			border: none;
			border-radius: 4px;
			background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
			text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
			color: #ffffff;
			font-size: 14px;
			font-weight: 400;
			text-transform: uppercase;
			margin-top: 19px;
  	&_phone {} }
		display: block;
		margin-top: 8px;
		color: #ffffff;
		font-size: 22px;
		font-weight: 400;
		text-transform: uppercase;
		text-align: right;
		line-height: 12px; } }
.promo {
	min-height: 800px;
	background: url('../img/bg/main_bg.jpg') center (center / cover) no-repeat;
	padding: 150px 0 135px 0;
	&_header {
		color: #ffffff;
		font-weight: 700;
		text-transform: uppercase;
		font-size: 60px;
		font-weight: 900;
		text-align: center;
		line-height: 60px;
		margin: 0;
		text-align: center; }
	&_subheader {
		color: #ffffff;
		font-size: 36px;
		font-weight: 700;
		text-align: center;
		line-height: 36px;
		margin: 0;
		margin-top: 22px;
		text-align: center; }
	&_descr {
		color: #ffffff;
		font-size: 18px;
		font-weight: 400;
		line-height: 24px;
		text-align: center;
		margin-top: 23px; }
	&_btn {
		display: block;
		margin: 118px auto 0 auto;
		width: 259px;
		height: 63px;
		box-shadow: inset 0 -4px 0 rgba(0, 0, 0, 0.12);
		border: none;
		border-radius: 4px;
		background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
		text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
		color: #ffffff;
		font-size: 18px;
		font-weight: 400;
		text-transform: uppercase; } }
.reasons {
	min-height: 900px;
	padding: 71px 0 65px 0;
	&_item {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-top: 35px; }
	&_round {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 113px;
		height: 113px;
		background-color: #1eacc7;
		border-radius: 100%;
		margin-right: 26px; }
	&_descr {
		width: 341px;
		&_title {
			color: #1eacc7;
			font-size: 17px;
			font-weight: 400;
			line-height: 21px; }
		&_text {
			color: #202020;
			font-size: 15px;
			font-weight: 300;
			line-height: 21px; } } }
.label {
	margin: 0 auto;
	width: 112px;
	height: 24px;
	padding: 8px 11px 6px 12px;
	background-color: #000000;
	color: #ffffff;
	font-size: 11px;
	font-weight: 400;
	text-transform: uppercase;
	text-align: center; }
.title {
	color: #222222;
	font-size: 38px;
	font-weight: 700;
	text-transform: uppercase;
	text-align: center;
	margin-top: 38px; }

.app {
	min-height: 864px;
	background: url('../img/bg/app_bg.jpg') center (center / cover) no-repeat;
	padding: 48px 0 80px 0;
	&_subheader {
		color: #222222;
		font-size: 17px;
		font-weight: 400;
		text-align: center;
		margin-top: 19px; }
	&_item {
		position: relative;
		width: 320px;
		height: 280px;
		margin: 0 auto;
		padding-top: 92px;
		&_1 {
			background: url('../img/app/1.png') center (center / cover) no-repeat; }
		&_2 {
			background: url('../img/app/2.png') center (center / cover) no-repeat; }
		&_3 {
			background: url('../img/app/3.png') center (center / cover) no-repeat; }
		&_4 {
			background: url('../img/app/4.png') center (center / cover) no-repeat; }
		&_5 {
			background: url('../img/app/5.png') center (center / cover) no-repeat; }
		&_6 {
			background: url('../img/app/6.png') center (center / cover) no-repeat; }
		&_title {
			color: #ffffff;
			font-size: 24px;
			font-weight: 400;
			line-height: 31px;
			text-transform: uppercase;
			text-align: center; }
		&_plus {
			display: flex;
			justify-content: center;
			align-items: center;
			position: absolute;
			bottom: 35px;
			left: 50%;
			transform: translateX(-50%);
			width: 26px;
			height: 26px;
			border-radius: 100%;
			background-color: #000000;
			margin: 0 auto;
			cursor: pointer; } } }
